body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  
  .header-wrapper{
    text-align: center;
  }
  
  .main-logo {
    -webkit-animation: main-logo-spin infinite 5s linear;
            animation: main-logo-spin infinite 5s linear;
    height: 80px;
  }
  
  .main-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }

  @-webkit-keyframes main-logo-spin {
    from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  }

  @keyframes main-logo-spin {
    from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  }
  
  .main-title {
    font-size: 1.5em;
  }
  
  
  .main-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
  
  }
  
  .main-title h2 {
    color: crimson;
    text-align: center;
  }
  
  
@media screen and (max-width: 660px) {
    .main-title h2 {
        font-size: 1em
  }
}


